@forward 'normalize';

$primary-color: #212E5B;
$secondary-color: #C41D48;
$black-color: #000000;
$white-color: #FFFFFF;
$primary-color-light: #EFF7FF;

$font-heading: 'Rubik', sans-serif;
$font-normal: 400;
$font-bold: 700;

$wrap-width: 1350px;
$wrap-width-narrow: 900px;
$wrap-padding: 2rem;
$border-radius: 0.28rem;

$border-style: 1px solid #D9D9D9;

* {
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
    scroll-padding-top: 5rem;
}

body {
    font-family: $font-heading;
    font-size: 18px;
    overflow-x: hidden;
    padding-top: 5rem;
}

.fancy-anchor a, p a {
    display: inline-block;
    &:after {
        content:'';
        width: 0;
        height: 2px;
        display: block;
        background: $white-color;
        border-radius: 2px;
        position: relative;
        top: 2px;
        transition: 1s all ease;
    }

    &:hover:after {
        width: 100%;
        transition: 1s all ease;
    }
}

p {
    line-height: 1.4;

    a {
        color: $primary-color;
        text-decoration: none;
        font-weight: $font-bold;

        &:after {
            background: $primary-color;
            top: 0px;
        }
    }
}

h2.ul-dots-center {
    position: relative;
     &:after {
        content: '...';
        color: $secondary-color;
        position: absolute;
        left: 0;
        right: 0;
        bottom: -1.5rem;
        font-size: 2.7rem;
        font-family: serif;
     }
}

.fixed-offer-btn {
    transform: rotate(90deg);
    position: fixed;
    left: 0;
    top: 50%;
    background: $secondary-color;
    color: $white-color;
    padding: 1rem 1rem 2rem 1rem;
    z-index: 9999;
    margin-left: -4.5rem; 
    border-radius: $border-radius $border-radius 0 0;
    text-decoration: none;
    font-weight: $font-bold;
    transition: all 0.5s ease;
    @media (max-width: 980px) {
        transform: rotate(-90deg);
        margin-left: auto;
        margin-right: -4.5rem;
        right: 0;
        left: auto;
        top: auto;
        bottom: 10%;
    }

    &:hover {
        margin-left: -4rem;
        transition: all 0.5s ease;
        @media (max-width: 980px) {
            margin-left: auto;
            margin-right: -4rem;
        }
    }
}

main button, .btn {
    background: $primary-color;
    color: $white-color;
    border: none;
    padding: 1.11111rem 1.66667rem;
    border-radius: $border-radius;
    font-size: 1rem;
    text-transform: lowercase;
    font-weight: $font-bold;
    display: inline-block;
    cursor: pointer;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    transition: all 0.5s ease;
    width: auto;
    justify-self: start;
    text-decoration: none;

    &:hover {
        background: $secondary-color;
    }

    &.red {
        background: $secondary-color;

        &:hover {
            background: $primary-color;
        }
    }
}

.wrap {
    max-width: $wrap-width;
    margin: 0 auto;
    width: 100%;
    padding: 0 $wrap-padding;
}

.wrap-narrow {
    max-width: $wrap-width-narrow;
    margin: 0 auto;
    width: 100%;
    padding: 0 $wrap-padding;
}

.wrap-wide {
    position: relative;
    width: calc(100% - $wrap-padding);
    @media (min-width: $wrap-width) {
        width: calc(100% - ((100% - $wrap-width) / 2) - $wrap-padding);
    }
    @media (min-width: 1700px) {
        width: 1500px;
        left: calc((100% - $wrap-width) / 2 - (1500px - $wrap-width + $wrap-padding));
        &:before {
            content:'';
            width: 0;
            height: 100%;
            display: block;
            position: absolute;
            top:0;
            left:-4px;
            border-left: 8px dotted $white-color;
        }
    }

    &.wrap-reverse {
        left: $wrap-padding;
        @media (min-width: $wrap-width) {
            left: calc((100% - $wrap-width) / 2 + $wrap-padding);
        }
        @media (min-width: 1700px) {
            &:before {
                right:-4px;
                left: auto;
            }
        }
    }

    .wrap {
        max-width: calc($wrap-width - $wrap-padding);

        &.wrap-absolute {
            position: absolute;
            top: 0;
            right: 0;
        }

        &.with-narrow {
            max-width: calc($wrap-width - 2 * $wrap-padding);
            padding: 0;
            margin: 0 0 0 auto;
            @media (max-width: $wrap-width) and (min-width: calc($wrap-width - $wrap-padding)) {
                margin-left: calc($wrap-padding - 0.5 * ($wrap-width - 100vw));
            }
    
            @media (max-width: calc($wrap-width - $wrap-padding)) and (min-width: calc($wrap-width-narrow + $wrap-padding)) {
                margin-left: calc($wrap-padding / 2);
            }

            @media (max-width: calc($wrap-width-narrow + $wrap-padding)) and (min-width: $wrap-width-narrow) {
                margin-left: calc($wrap-padding / 2 - 0.5 * ($wrap-width-narrow + $wrap-padding - 100vw));
    
            }

            @media (max-width: $wrap-width-narrow) {
                margin-left: 0;
            }
        }
    }
    .wrap-narrow {
        width: 100%;  
    }
}

header {
    position: fixed;
    width: 100%;
    top: 0;
    background: $white-color;
    z-index: 9000;

    &.scrolling {
        
        nav .header-logo .ma-logo {
            height: 0;
            overflow: hidden;
            transition: all 0.5s ease;
        }
        nav .header-logo h1 {
            margin-bottom: 0;
            transition: all 0.5s ease;
        }
    }

    nav {
        display: flex;
        justify-content: space-between;
        position: relative;
        @media (max-width: 980px) {
            align-items: center;
        }

        .header-logo {
            text-decoration: none;
            padding: 1rem 0;
            h1 {
                font-size: 1.11rem;
                line-height: 1.2;
                text-transform: uppercase;
                color: $secondary-color;
                margin: 0 0 0.5rem;

                span {
                    color: $primary-color;
                }
            }

            .ma-logo {
                color: $primary-color;
                font-size: 0.77778rem;
                text-transform: uppercase;
                display: flex;
                align-items: center;
                gap: 0.5rem;
                height: 19px;
                transition: all 0.5s ease;
                img {
                    width: 27px;
                }
            }
        }

        .header-right {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding-bottom: 0.5rem;

            @media (max-width: 980px) {
                position: absolute;
                top: 100%;
                right: -2rem;
                background-color: $primary-color-light;
                border-radius: 0 0 0 $border-radius;
                width: 18.5rem;
                transform: translateX(100%);
                transition: 1s all ease;
            }

            &.mobile-menu-open {
                @media (max-width: 980px) {
                    transition: 1s all ease;
                    transform: translateX(0);
                }               
            }

            .header-right-top {
                display: flex;
                justify-content: flex-end;
                align-items: center;
                gap: 1rem;
                @media (max-width: 980px) {
                    flex-direction: column;
                    align-items: flex-start;
                }

                .info-line {
                    background: $primary-color;
                    color: $white-color;
                    font-size: 0.77778rem;
                    display: flex;
                    align-items: center;
                    padding: 0.5rem 1rem;
                    gap: 1rem;
                    border-radius: 0 0 $border-radius $border-radius;
                    @media (max-width: 980px) {
                        flex-direction: column;
                        width: 100%;
                        border-radius: 0 0 0 $border-radius;
                        align-items: flex-start;
                        gap: 0.3rem;
                        padding: 0.8rem 1rem;
                    }

                    a {
                        color: $white-color;
                        text-decoration: none;
                        font-size: 1.33333rem;
                        font-weight: $font-bold;
                    }
                }

                .language-selector {
                    display: flex;
                    gap: 0.5rem;
                    @media (max-width: 980px) {
                        padding-left: 1rem;
                    }

                    a {
                        text-decoration: none;
                        color: $primary-color;
                        font-size: 0.9rem;

                        &.current-language {
                            color: $secondary-color;
                            font-weight: $font-bold;
                        }
                    }
                }
            }

            .header-right-bottom {
                display: flex;
                justify-content: flex-end;
                align-items: center;
                gap: 1rem;
                padding-top: 0.5rem;
                @media (max-width: 980px) {
                    flex-direction: column;
                    padding-top: 1rem;
                    align-items: flex-start;
                    padding-left: 1rem;
                }

                .main-menu {
                    display: flex;
                    gap: 1rem;
                    @media (max-width: 980px) {
                        flex-direction: column;
                        gap: 1.4rem;
                        align-items: flex-start;
                    }
                    a {
                        color: $primary-color;
                        text-decoration: none;
                        text-transform: uppercase;
                        font-size: 0.88889rem;
                        font-weight: $font-bold;
                        display: flex;
                        flex-direction: column;
                        
                        .menu-underline {
                            width: 100%;
                            display: flex;
                            gap: 4px;

                            .menu-dot {
                                width: 4px;
                                height: 4px;
                                border-radius: 2px;
                                background: $secondary-color;
                            }
                            .dot-underline {
                                transition: all 1s;
                            }

                        }

                        &:hover {
                            .dot-underline {
                                width: calc(100% - 16px);
                                transition: all 1s;
                            }
                            
                        }
                    }

                }

                .social-links {
                    display: flex;
                    gap: 1rem;
                    @media (max-width: 980px) {
                        gap: 0.75rem;
                    }
                    a {
                        color: $primary-color;
                        @media (max-width: 980px) {
                            font-size: 2rem;
                        }

                        &:hover {
                            color: $secondary-color
                        }
                    }
                }

            }

        }

        .mobile-menu-trigger {
            display: none;
            width: 3rem;
            height: 3rem;
            border: none;
            border-radius: 2rem;
            background: $primary-color;
            color: $white-color;

            @media (max-width: 980px) {
                display: inline-block;
            }
        }
    }
}

main {
    padding: 2rem 0;
}

.featured-img-section {

    .wrap {
        height: 100%;
        display: flex;
        align-items: center;
    }

    h2 {
        font-size: 2.66rem;
        text-transform: uppercase;
        color: $white-color;
        @media (max-width: 800px) {
            font-size: 1.66rem;
        }

        &:before, &:after {
            content: '';
            width: 7rem;
            height: 0.5rem;
            border-radius: .5rem;
            background: $white-color;
            display: block;
            @media (max-width: 800px) {
                width: 5rem;
                height: 0.25rem;
            }
        }

        &:before {
            margin-bottom: 1rem;
        }

        &:after {
            margin-top: 1rem;
        }
    }

    figure {
        width: 100%;

        img {
            width: 100%;
            border-radius: 0 $border-radius $border-radius 0;
        }
    }
}

/* HOME */

.welcome-section {
    padding: 3rem 0;

    h2 {
        color: $primary-color;
    }

    .what-we-do {
        display: flex;
        margin-top: 3rem;
        justify-content: space-between;
        @media (max-width: 600px) {
            justify-content: flex-start;
            align-items: flex-start;
            flex-direction: column;
            gap: 1rem;
        }

        div {
            display: flex;
            flex: 0 0 auto;
            align-items: center;

            h3 {
                text-transform: uppercase;
                font-size: 1.333rem;
                margin: 0;
                @media (max-width: 800px) {
                    font-size: 1.2rem;
                }

                br {
                    @media (max-width: 600px) {
                        display: none;
                    }
                }
            }

            img {
                @media (max-width: 800px) {
                    width: 65px;
                }
                @media (max-width: 600px) {
                    margin-right: 1rem;
                }
            }
        }
    }
}

.products-home {
    margin: 2rem 0;
     h2 {
        color: $primary-color;
        text-transform: uppercase;
        font-size: 2rem;
        text-align: center;
     }

     .product-tiles {
        display: flex;
        justify-content: center;
        flex-direction: column;
        padding: 2rem 0;
        .rows {
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
        }
        .product-tile {
            padding: 1rem;
            color: $primary-color;
            text-transform: lowercase;
            font-size: 1.333rem;
            text-align: center;
            text-decoration: none;
            font-weight: $font-bold;

            figure {
                background: url('../img/tilted-rect-light.svg') no-repeat;
                background-size: 100%;
                background-position: center;
                padding: 1rem 1rem 0;
                margin-bottom: 1rem;

                img{
                    transition: all 1s ease;
                }
            }

            &:hover {
                color: $secondary-color;
                img {
                    transform: scale(1.1);
                    transition: all 1s ease;
                }
            }

        }

     }
}

.numbers {
    color: $white-color;
    margin: 2rem 0;
    .wrap-wide {
        background: $primary-color;
        border-radius:  $border-radius 0 0 $border-radius;
        padding-top: 2rem;
        padding-bottom: 2rem;
        position: relative;

        &:after {
            content: '...';
            color: $secondary-color;
            position: absolute;
            left: 1.5rem;
            bottom: -1.45rem;
            font-size: 5rem;
            font-family: serif;
         }

        .wrap {
            margin-right: auto;
            margin-left: 0;

            h2 {
                margin: 0;
                font-size: 2rem;
            }

            .numbers-container {
                display: flex;
                justify-content: space-around;
                flex-wrap: wrap;
                
                .number-tile {
                    text-align: center;
                    padding: 1rem 2rem 0;
                    @media (max-width: 1050px) {
                        padding: 1rem 1rem 0;
                    }

                    h3 {
                        font-size: 5.5rem;
                        color: $secondary-color;
                        font-weight: $font-bold;
                        margin: 0;
                        @media (max-width: 1050px) {
                            font-size: 3.5rem;
                        }
                    }

                    p {
                        margin: 0;
                    }
                }
            }
        }
    }
}

.applications {
    margin: 4rem 0;
    h2 {
        color: $primary-color;
        text-transform: uppercase;
        font-size: 2rem;
        text-align: center;
    }
}

.partners {
    margin: 4rem 0;
    .wrap {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: flex-start;
        @media (max-width: 700px) {
            justify-content: center;
        }

        h2 {
            color: $primary-color;
            text-transform: uppercase;
            font-size: 1.333rem;
            margin: 0;
            @media (max-width: 700px) {
                text-align: center;
                width: 100%;
                margin-bottom: 1rem;
                font-size: 1rem;
            }
        }

        img {
            width: auto;
            height: 3rem;
            margin: 1rem;
            @media (max-width: 700px) {
                height: 2rem;
                margin: 0.5rem;
            }
        }
    }
}

.certs {
    margin: 4rem 0 0;
    .wrap-narrow {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: space-evenly;

        img {
            width: auto;
            height: 3.5rem;
            margin: 1rem;

            @media (max-width: 600px) {
                height: 2rem;
                margin: 0.5rem;
            }
        }
    }

}

.landing-link {
    margin: 4rem 0;
    .landing-link-container {
        border: $border-style;
        border-radius: $border-radius;
        padding: 2rem 2rem 0;
        text-align: center;

        img {
            width: 7rem;
        }

        h2 {
            font-size: 2rem;
            text-transform: uppercase;
            color: $primary-color;
        }

        .btn {
            margin-bottom: -1rem;
        }
    }
}

.form-section {
    .wrap-wide {
        background: url('../img/form-bg.webp') no-repeat $primary-color;
        background-size: cover;
        background-position: right;
        border-radius: 0 $border-radius $border-radius 0;
        padding: 2rem 0;
    }

    h2 {
        color: $white-color;
        font-size: 2rem;
        margin-top: 0;
    }

    form {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
        @media (max-width: 600px) {
            grid-template-columns: 1fr;
            gap: 1rem 0
        }

        label {
            position: absolute;
            width: 1px;
            height: 1px;
            padding: 0;
            margin: -1px;
            overflow: hidden;
            clip: rect(0, 0, 0, 0);
            border: 0;
        }

        input {
            width: 100%;
            font-size: 1rem;
            padding: 0.8888rem;
            border-radius: $border-radius;
            border: none;
        }

        select {
            width: 100%;
            font-size: 1rem;
            padding: 0.8888rem;
            border-radius: $border-radius;
            border: none;
        }

        textarea {
            width: 100%;
            font-size: 1rem;
            padding: 0.8888rem;
            border-radius: $border-radius;
            height: 100%;
        }

        .form-name {
            grid-column: 1 / 2;
        }

        .form-email {
            grid-column: 2 / 3;
            @media (max-width: 600px) {
                grid-column: 1 / 2;
            }   
        }

        .form-target {
            grid-column: 1 / 2;
        }

        .form-phone {
            grid-column: 2 / 3;
            @media (max-width: 600px) {
                grid-column: 1 / 2;
            } 
        }

        .form-subject {
            grid-column: 1 / 3;
            margin: 1rem 0;
        }

        .form-message {
            grid-column: 1 / 3;
            height: 12rem;
        }

    }
}

footer {
    .footer-top {
        background: $primary-color;
        color: $white-color;
        padding: 3rem 0;
      
        .wrap {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            gap: 2rem;
            @media (max-width: 840px) {
                grid-template-columns: 1fr 1fr;
            }
            @media (max-width: 500px) {
                grid-template-columns: 1fr;
                padding: 0 2.5rem;
            }
        }

        .footer-col-1 {
             img {
                width: 5.5rem;
             }

             h4 {
                font-size: 1.8rem;
                text-transform: uppercase;
                margin: 0.3rem 0 0.6rem;
             }

             h5 {
                font-size: 1.33rem;
                text-transform: uppercase;
                font-weight: 300;
                margin: 0;
             }
        }

        h4.fat-footer-titles {
            font-size: 1.333rem;
            text-transform: uppercase;
            position: relative;
            margin-top: 0;
            margin-bottom: 2.5rem;

            &::after {
                content: '...';
                color: $secondary-color;
                position: absolute;
                left: 0;
                top: 0rem;
                font-size: 2.7rem;
                font-family: serif;
            }

        }

        .fat-footer-menu {
            display: flex;
            flex-direction: column;
            gap: 0.7rem;
            align-items: flex-start;

            a {
                font-size: 1.1rem;
                color: $white-color;
                text-decoration: none;
                font-weight: 300;
            }
        }

        .footer-col-4 {
            dl {
                dt {
                    font-weight: $font-bold;
                    margin-bottom: 0.25rem;
                }
    
                dd {
                    margin-bottom: 0.5rem;
                    margin-left: 0;
                    font-weight: 300;
                }
            }

            a {
                color: $white-color;
                text-decoration: none;
                font-weight: 300;
            }
        }
    }

    .footer-bottom {
        background: $black-color;
        padding: 1rem 0;
        color: $white-color;
        font-size: 0.888rem;
        .wrap {
            display: flex;
            justify-content: space-between;
            align-items: center;
            @media (max-width: 500px) {
                padding: 0 2.5rem;
                flex-direction: column;
                align-items: flex-start;
            }

            .footer-menu {
                display: flex;

                a {
                    color: $white-color;
                }
            }

        }
    }
}