}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input[type=search] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
}
input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}
input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 5rem;
}

body {
  font-family: "Rubik", sans-serif;
  font-size: 18px;
  overflow-x: hidden;
  padding-top: 5rem;
}

.fancy-anchor a, p a {
  display: inline-block;
}
.fancy-anchor a:after, p a:after {
  content: "";
  width: 0;
  height: 2px;
  display: block;
  background: #FFFFFF;
  border-radius: 2px;
  position: relative;
  top: 2px;
  transition: 1s all ease;
}
.fancy-anchor a:hover:after, p a:hover:after {
  width: 100%;
  transition: 1s all ease;
}

p {
  line-height: 1.4;
}
p a {
  color: #212E5B;
  text-decoration: none;
  font-weight: 700;
}
p a:after {
  background: #212E5B;
  top: 0px;
}

h2.ul-dots-center {
  position: relative;
}
h2.ul-dots-center:after {
  content: "...";
  color: #C41D48;
  position: absolute;
  left: 0;
  right: 0;
  bottom: -1.5rem;
  font-size: 2.7rem;
  font-family: serif;
}

.fixed-offer-btn {
  transform: rotate(90deg);
  position: fixed;
  left: 0;
  top: 50%;
  background: #C41D48;
  color: #FFFFFF;
  padding: 1rem 1rem 2rem 1rem;
  z-index: 9999;
  margin-left: -4.5rem;
  border-radius: 0.28rem 0.28rem 0 0;
  text-decoration: none;
  font-weight: 700;
  transition: all 0.5s ease;
}
@media (max-width: 980px) {
  .fixed-offer-btn {
    transform: rotate(-90deg);
    margin-left: auto;
    margin-right: -4.5rem;
    right: 0;
    left: auto;
    top: auto;
    bottom: 10%;
  }
}
.fixed-offer-btn:hover {
  margin-left: -4rem;
  transition: all 0.5s ease;
}
@media (max-width: 980px) {
  .fixed-offer-btn:hover {
    margin-left: auto;
    margin-right: -4rem;
  }
}

main button, .btn {
  background: #212E5B;
  color: #FFFFFF;
  border: none;
  padding: 1.11111rem 1.66667rem;
  border-radius: 0.28rem;
  font-size: 1rem;
  text-transform: lowercase;
  font-weight: 700;
  display: inline-block;
  cursor: pointer;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  transition: all 0.5s ease;
  width: auto;
  justify-self: start;
  text-decoration: none;
}
main button:hover, .btn:hover {
  background: #C41D48;
}
main button.red, .btn.red {
  background: #C41D48;
}
main button.red:hover, .btn.red:hover {
  background: #212E5B;
}

.wrap {
  max-width: 1350px;
  margin: 0 auto;
  width: 100%;
  padding: 0 2rem;
}

.wrap-narrow {
  max-width: 900px;
  margin: 0 auto;
  width: 100%;
  padding: 0 2rem;
}

.wrap-wide {
  position: relative;
  width: calc(100% - 2rem);
}
@media (min-width: 1350px) {
  .wrap-wide {
    width: calc(100% - (100% - 1350px) / 2 - 2rem);
  }
}
@media (min-width: 1700px) {
  .wrap-wide {
    width: 1500px;
    left: calc((100% - 1350px) / 2 - (150px + 2rem));
  }
  .wrap-wide:before {
    content: "";
    width: 0;
    height: 100%;
    display: block;
    position: absolute;
    top: 0;
    left: -4px;
    border-left: 8px dotted #FFFFFF;
  }
}
.wrap-wide.wrap-reverse {
  left: 2rem;
}
@media (min-width: 1350px) {
  .wrap-wide.wrap-reverse {
    left: calc((100% - 1350px) / 2 + 2rem);
  }
}
@media (min-width: 1700px) {
  .wrap-wide.wrap-reverse:before {
    right: -4px;
    left: auto;
  }
}
.wrap-wide .wrap {
  max-width: calc(1350px - 2rem);
}
.wrap-wide .wrap.wrap-absolute {
  position: absolute;
  top: 0;
  right: 0;
}
.wrap-wide .wrap.with-narrow {
  max-width: calc(1350px - 4rem);
  padding: 0;
  margin: 0 0 0 auto;
}
@media (max-width: 1350px) and (min-width: calc(1350px - 2rem)) {
  .wrap-wide .wrap.with-narrow {
    margin-left: calc(2rem - 0.5 * (1350px - 100vw));
  }
}
@media (max-width: calc(1350px - 2rem)) and (min-width: calc(900px + 2rem)) {
  .wrap-wide .wrap.with-narrow {
    margin-left: 1rem;
  }
}
@media (max-width: calc(900px + 2rem)) and (min-width: 900px) {
  .wrap-wide .wrap.with-narrow {
    margin-left: calc(1rem - 0.5 * (900px + 2rem - 100vw));
  }
}
@media (max-width: 900px) {
  .wrap-wide .wrap.with-narrow {
    margin-left: 0;
  }
}
.wrap-wide .wrap-narrow {
  width: 100%;
}

header {
  position: fixed;
  width: 100%;
  top: 0;
  background: #FFFFFF;
  z-index: 9000;
}
header.scrolling nav .header-logo .ma-logo {
  height: 0;
  overflow: hidden;
  transition: all 0.5s ease;
}
header.scrolling nav .header-logo h1 {
  margin-bottom: 0;
  transition: all 0.5s ease;
}
header nav {
  display: flex;
  justify-content: space-between;
  position: relative;
}
@media (max-width: 980px) {
  header nav {
    align-items: center;
  }
}
header nav .header-logo {
  text-decoration: none;
  padding: 1rem 0;
}
header nav .header-logo h1 {
