    flex-direction: column;
    align-items: flex-start;
  }
}
header nav .header-right .header-right-top .info-line {
  background: #212E5B;
  color: #FFFFFF;
  font-size: 0.77778rem;
  display: flex;
  align-items: center;
  padding: 0.5rem 1rem;
  gap: 1rem;
  border-radius: 0 0 0.28rem 0.28rem;
}
@media (max-width: 980px) {
  header nav .header-right .header-right-top .info-line {
    flex-direction: column;
    width: 100%;
    border-radius: 0 0 0 0.28rem;
    align-items: flex-start;
    gap: 0.3rem;
