  }
}
header nav .header-right .header-right-top .info-line a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 1.33333rem;
  font-weight: 700;
}
header nav .header-right .header-right-top .language-selector {
  display: flex;
  gap: 0.5rem;
}
@media (max-width: 980px) {
  header nav .header-right .header-right-top .language-selector {
    padding-left: 1rem;
  }
}
header nav .header-right .header-right-top .language-selector a {
  text-decoration: none;
  color: #212E5B;
  font-size: 0.9rem;
}
header nav .header-right .header-right-top .language-selector a.current-language {
  color: #C41D48;
