}
header nav .header-right .header-right-bottom {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 1rem;
  padding-top: 0.5rem;
}
@media (max-width: 980px) {
  header nav .header-right .header-right-bottom {
    flex-direction: column;
    padding-top: 1rem;
    align-items: flex-start;
    padding-left: 1rem;
  }
}
header nav .header-right .header-right-bottom .main-menu {
  display: flex;
  gap: 1rem;
}
@media (max-width: 980px) {
  header nav .header-right .header-right-bottom .main-menu {
    flex-direction: column;
    gap: 1.4rem;
    align-items: flex-start;
  }
}
header nav .header-right .header-right-bottom .main-menu a {
  color: #212E5B;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 0.88889rem;
  font-weight: 700;
