    gap: 0.75rem;
  }
}
header nav .header-right .header-right-bottom .social-links a {
  color: #212E5B;
}
@media (max-width: 980px) {
  header nav .header-right .header-right-bottom .social-links a {
    font-size: 2rem;
  }
}
header nav .header-right .header-right-bottom .social-links a:hover {
  color: #C41D48;
}
header nav .mobile-menu-trigger {
  display: none;
  width: 3rem;
  height: 3rem;
  border: none;
